import React, { useState, useEffect } from 'react'
import { useForm } from 'react-hook-form'
import {  useSelector, useDispatch } from 'react-redux'
import { Card, CardHeader, CardTitle, CardBody, Row, Col, Button, FormGroup, Form, Label } from 'reactstrap'
import Uppy from '@uppy/core'
//import {EditorState} from "draft-js"
import { DragDrop } from '@uppy/react'
import UiLoader from '@components/ui-loader'
import Select from 'react-select'
import { selectThemeColors } from '@utils'
//import { Editor } from 'react-draft-wysiwyg'
import { EditorState } from 'draft-js'
import { Editor } from 'react-draft-wysiwyg'
import { convertToHTML } from 'draft-convert'
import DOMPurify from 'dompurify'
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css'
//import htmlToDraft from 'html-to-draftjs'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/react/libs/editor/editor.scss'
//import { EditorState, ContentState } from 'draft-js'
import { addFeedPost, addPostReset } from '../../store/action'
import { notifyWarning } from '../../../Toast'
import Breadcrumbs from '@components/breadcrumbs'
import './styles.scss'

const AddPost = () => {
 
  const { handleSubmit, reset } = useForm()
  const dispatch = useDispatch()
  const store = useSelector(state => state.feeds)

  // const contentBlock = htmlToDraft(initialContent)
  // const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks)
  // const editorState = EditorState.createWithContent(contentState)
  //const [editorState, setEditorState] = useState(EditorState.createEmpty())
  const [imageUpload, setImageUpload] = useState('')
  const [imageError, setImageError] = useState(false)
  const [content, setContent] = useState("")
//  const onEditorStateChange = editorState => {
//   setEditorState(editorState)
//   console.log(editorState)
// }
  //console.log(editorState)
  const categoryOptions = [
    { value: '', label: 'Select Category' },
    { value: 'Post', label: 'Post' },
    { value: 'Announcement', label: 'Announcement' },
    { value: 'Offers', label: 'Offers' },
    { value: 'Stats', label: 'Stats' },
    { value: 'Merchandise', label: 'Merchandise' },
    { value: 'Countdown', label: 'Countdown' },
    { value: 'Polls', label: 'Polls' },
    { value: 'Winner Announcement', label: 'Winner Announcement' }
  ]

  // const langOptions = [
  //   { value: '1', label: 'English' },
  //   { value: '2', label: 'Arabic' }
  // ]

  const [currentCategory, setCurrentCategory] = useState({
    value: '',
    label: 'Select Category'
  })
  // const [currentlang, setCurrentLang] = useState()
  const handleCurrentCategory = (val) => {
    setCurrentCategory(val)
  }
  // const handleCurrentlang = (val) => {
  // setCurrentLang(val)
  // }
 
  const [editorState, setEditorState] = useState(
    () => EditorState.createEmpty('')
  )
  useEffect(() => {}, [editorState])
  const  [convertedContent, setConvertedContent] = useState(null)
  const convertContentToHTML = (editorState) => {
    const currentContentAsHTML = convertToHTML(editorState.getCurrentContent())
    setConvertedContent(currentContentAsHTML)
}
  
  const handleEditorChange = (state) => {
    setEditorState(state)
    convertContentToHTML(state)
  }

  const createMarkup = (html) => {
    return  {
      __html: DOMPurify.sanitize(html)
    }
    
  }


  const [editor, setEditor] = useState(
    () => EditorState.createEmpty()
  )
  useEffect(() => {}, [editor])
  const  [converted, setConverted] = useState(null)
  // const convertC = () => {
  //   const currentContentAsHTML = convertToHTML(editor.getCurrentContent())
  //   setConverted(currentContentAsHTML)
  // }
  const convertC = (editor) => {
    const currentContentAsHTML = convertToHTML(editor.getCurrentContent())
    setConverted(currentContentAsHTML)
}
  
  const handleEditor = (state) => {
    setEditor(state)
    convertC(state)
  }
 useEffect(() => () => {
    dispatch(addPostReset())
  }, [])
  
  useEffect(() => {    
    if (store.addSuccessData) {
      reset()
      setContent({editor:content?.editor})
      console.log(content)
      setImageUpload('')
      setEditorState('')
      setTimeout(() => {
        dispatch(addPostReset())
      }, 3000)
    }
  }, [store.addSuccessData])
  
  const uppy = new Uppy({
    meta: { type: 'avatar' },
    autoProceed: true,
    restrictions: { maxNumberOfFiles: 1, allowedFileTypes: ['image/*'] }
  })
  uppy.on("file-added", function (file) {
    const reader = new FileReader()
    reader.readAsDataURL(file.data)
    reader.onloadend = function () {
      const base64data = reader.result
      setImageUpload(base64data)
    }
  })
  const dangerouslySetInnerHTML = createMarkup(convertedContent).__html
  const feedtext = (dangerouslySetInnerHTML.length < 20 ? null : dangerouslySetInnerHTML)

  const dangerouslySet = createMarkup(converted).__html
  const feedtex = (dangerouslySet.length < 20 ? null : dangerouslySet)
  
  const formSubmit = (val) => {
    if (imageUpload) {
      console.log(val)
     
      const params = {
        feed_type: "Post",
        feed_content: feedtext,
        feed_image: imageUpload,
        // feed_lang: currentlang.value,
        arabic_feed_content: feedtex
      }
      console.log(val.post_content)
      console.log(imageUpload)
      console.log(params.feed_image)
      
      if ((params.feed_content === null || params.feed_image === '')) {
        notifyWarning()
      } else {
        dispatch(addFeedPost(params))
      }
      setConvertedContent('')
    }
  }

  const formHandler = (e) => {
    e.preventDefault()

    if (store.addPostLoader) {
      return
    }

    if (!imageUpload) {
      setImageError(true)
    } else {
      setImageError(false)
    }

    handleSubmit(formSubmit)()
  }
  
  return (
  <>
          <Breadcrumbs breadCrumbTitle='Walls' breadCrumbParent='Walls' breadCrumbActive='Add' />
    <Card>
      <CardHeader>
        <CardTitle tag="h4">Add Post</CardTitle>
      </CardHeader>
      <CardBody>
    
          {/* <Col className='mb-1' md='6' sm='12'>
            <Label>Wall language</Label>
            <Select
              theme={selectThemeColors}
              className='react-select'
              classNamePrefix='select'
              value={currentlang}
              onChange={(data) => {
                handleCurrentlang(data)
              }}
              name='clear'
              options={langOptions}
              isClearable
            /> 
          </Col> */}
        
        <Row className="wrapper-add-post">
          <Col className="add-post-edit">
            <Form onSubmit={formHandler}>
              <div className="form-label-group add-feed-textarea">
                <FormGroup>
                  <Label for="nameVertical">Feed Type</Label>
                  <Select
                    isClearable={false}
                    theme={selectThemeColors}
                    className="react-select"
                    classNamePrefix="select"
                    options={categoryOptions}
                    value={currentCategory}
                    onChange={(data) => {
                      handleCurrentCategory(data)
                    }}
                  />
                  <br/>
                  <Label className="form-label" for="post_content">
                   <h3>Post Content English</h3> 
                  </Label>
                 
                    <Editor 
                        editorState={editorState}
                        onEditorStateChange={handleEditorChange}
                        wrapperClassName="wrapper-class"
                        editorClassName="editor-class"
                        toolbarClassName="toolbar-class"
                        // wrapperStyle={{ width: 800, border: "1px solid black" }}
                   />
                   <br/>
                   <Label className="form-label" for="post_content">
                    <h3>Post Content Arabic</h3>
                  </Label>
                 
                    <Editor 
                        editorState={editor}
                        onEditorStateChange={handleEditor}
                        wrapperClassName="wrapper-class"
                        editorClassName="editor-class"
                        toolbarClassName="toolbar-class"
                      // wrapperStyle={{ width: 800, border: "1px solid black" }}
                   />
                 
                </FormGroup>
              </div>
              <div className={`mt-4 ${imageError ? 'img-error-post' : ''}`}>
                <DragDrop
                  uppy={uppy}
                  locale={{
                    strings: {
                      // Text to show on the droppable area.
                      // `%{browse}` is replaced with a link that opens the system file selection dialog.
                      dropHereOr: 'Drop Image or %{browse}',
                      // Used as the label for the link that opens the system file selection dialog.
                      browse: 'browse'
                    }
                  }}
                />
                {imageUpload && (
                  <div className="uploaded-files">
                    <img src={imageUpload} className="post-preview" />
                  </div>
                )}
              </div>
              <FormGroup className="mt-3">
                <div className="d-flex justify-content-start">
                  <Button.Ripple
                    type="submit"
                    color="primary"
                    disabled={store.addPostLoader}
                    className="btn-next"
                  >
                    <span className="align-middle d-sm-inline-block d-none">
                      Add Post
                    </span>
                  </Button.Ripple>
                  {store.addPostLoader && (
                    <div className="ml-2 mt-2">
                      <UiLoader blocking />
                    </div>
                  )}
                  {store.addSuccessData && (
                    <p className="ml-2 pt-1 mb-0 success-msg">
                      Post Added Successfull
                    </p>
                  )}
                </div>
              </FormGroup>
            </Form>
          </Col>
        </Row>
      </CardBody>
    </Card>
    </>
  )
}

export default AddPost